function useQueryFake() {
  return {
    loading: false,
    error: false,
    data: {
      payoutDetails: payoutDetails,
    }
  }
};

function toPayoutStatusTag(status) {
  if (status === 'paid')
    return <Badge>Paid</Badge>;
  if (status === 'in_transit')
    return <Badge status="info">In transit</Badge>;
  if (status === 'pending')
    return <Badge status="info">Pending</Badge>;
  return <Badge>Paid</Badge>;
}

